.App {
  text-align: center;
  min-height: 100vh;
  background-color: #faaca8;
  background-image: linear-gradient(19deg, #faaca8 0%, #ddd6f3 100%);
}

ol {
  list-style: none;
}

.search_form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search_bar {
  width: 50%;
  border: none;
  padding: 10px;
  outline: none;
  border-radius: 10px;
}

.search_button {
  background-color: lightcoral;
  padding: 10px 20px;
  border: none;
  color: white;
  margin-left: 20px;
  display: block;
  width: 10%;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
}
.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.recipe_box {
  width: 40%;
  margin: auto;
  border-radius: 10px;
  box-shadow: 0px 5px 20px rgb(71, 71, 71);
  margin-top: 30px;
  background-color: #fff;
  cursor: pointer;
}

.recipe_box > img {
  border-radius: 10px;
  margin-bottom: 10px;
}

.recipe_title {
  border-radius: 10px;
  box-shadow: 0px 5px 20px rgb(71, 71, 71);
  display: flex;
  justify-content: center;
  align-items: center;

  width: 80%;
  margin: auto;
  margin-top: 10px;
}
